import React from "react";
import User2 from "../images/user2.svg";
import User3 from "../images/user3.svg";
import User4 from "../images/user4.svg";
import "../css/Rooms.css";
import { Link } from "react-router-dom";
import Button from "@material-ui/core/Button";

const Rooms = () => {
  return (
    <>
      <Link to="/traderoom/WallStreetBets" className="room">
        <p>WallStreetBets</p>
        <img src={User2}></img>
        <div>
          <i class="fas fa-users">12k visitors</i>
          <i class="fas fa-thumbs-up">25.4k</i>
        </div>
      </Link>
      <Link to="/traderoom/The Future of Bitcoin and Crypto?" className="room">
        <p>The Future of Bitcoin and Crypto?</p>
        <img src={User3}></img>
        <div>
          <i class="fas fa-users">9.1k visitors</i>
          <i class="fas fa-thumbs-up">17.4k</i>
        </div>
      </Link>
      <Link
        to="/traderoom/New Rising Stars in the EV Industry"
        className="room"
      >
        <p>New Rising Stars in the EV Industry</p>
        <img src={User4}></img>
        <div>
          <i class="fas fa-users">6.8k visitors</i>
          <i class="fas fa-thumbs-up">4.3k</i>
        </div>
      </Link>
      <Button component={Link} to="/featuretba" className="start-a-room">
        + Start a room
      </Button>
    </>
  );
};

export default Rooms;
