import React from "react";
import { Link } from "react-router-dom";
import User1 from "../images/user1.svg";
import User2 from "../images/user2.svg";
import User3 from "../images/user3.svg";
import User4 from "../images/user4.svg";
import "../css/GroupRoom.css";
import { Button } from "@material-ui/core";

const GroupRoom = (props) => {
  return (
    <>
      <div className="gr-nav">
        <Link to="/dash">
          <i class="fas fa-arrow-left"></i>
        </Link>
        <img src={User1}></img>
      </div>

      <h1 className="gr-h">{props.match.params.title}</h1>

      <div className="gr-sub">
        <p>Adipiscing vel aliquam praesent.</p>
        <button>Stock List</button>
      </div>

      <div className="gr-sub-card">
        <img src={User2}></img>
        <i class="fas fa-microphone"></i>
        <div>
          <p>John Doe</p>
          <p>18.4k Subscribers</p>
          <p>SUBSCRIBE</p>
        </div>
      </div>

      <div className="gr-sub-chat">
        <div className="gr-box">
          <img src={User3}></img>
          <div className="gr-text">
            <p>Nancy</p>
            <h3>Nice Point</h3>
          </div>
        </div>
        <div className="gr-box">
          <img src={User4}></img>
          <div className="gr-text">
            <p>Jonathan</p>
            <h3>I subscribed you</h3>
          </div>
        </div>
        <div className="gr-box">
          <img src={User1}></img>
          <div className="gr-text">
            <p>Sam</p>
            <h3>Nice information!</h3>
          </div>
        </div>
        <div className="gr-box">
          <img src={User3}></img>
          <div className="gr-text">
            <p>Nancy</p>
            <h3>Interesting!!</h3>
          </div>
        </div>
        <div className="gr-msg">
          <input placeholder="Enter Message"></input>
          <p>Send</p>
          <p>Raise Hand</p>
        </div>
        <Link to="/">
          <Button>Next</Button>
        </Link>
      </div>
    </>
  );
};

export default GroupRoom;
